import React, { Fragment, useContext } from "react";
import PropTypes from "prop-types";
import BlogContext from "../../../context/blog/blogContext";
import { useHistory } from "react-router-dom";

const BlogItem = ({ blog }) => {
    const blogContext = useContext(BlogContext);
    const { setCurrent } = blogContext;
    let history = useHistory();
    const { blog_headline, blog_subtitle, user_name } = blog;

    // const onDelete = () => {
    // 	deleteBlog(pk_df_blog_page_id);
    // 	clearCurrent();
    // };

    const check = () => {
        setCurrent(blog);
        history.push("/formadmin");
    };

    return (
        <Fragment>
            <div style={{ padding: "1rem", fontFamily: 'Open Sans, sans-serif' }} className="card bg-light">
                <h3 style={{ fontWeight: 700 }}>{blog_headline}</h3>
                <h5>{blog_subtitle}</h5>
                <h6 style={{ fontWeight:400 }}>By {user_name}</h6>

                <br></br>
                <p>
                    <button className="btn btn-primary btn-sm" onClick={check}>
                        Check
                    </button>
                </p>
            </div>
            <br />
        </Fragment>
    );
};

BlogItem.propType = {
    blog: PropTypes.object.isRequired,
};

export default BlogItem;
