import React, { useReducer } from 'react';
import BlogContext from './blogContext';
import { EditorState, convertFromRaw } from 'draft-js';
import blogReducer from './blogReducer';

import {
	GET_BLOGS,
	CLEAR_BLOGS,
	ADD_BLOG,
	DELETE_BLOG,
	SET_CURRENT,
	CLEAR_CURRENT,
	UPDATE_BLOG,
} from '../types';

import axios from 'axios';
import backend_url from '../../url';

const BlogState = (props) => {
	const initialState = {
		blogs: [],
		current: null,
	};

	const [state, dispatch] = useReducer(blogReducer, initialState);

	//add BLOG
	const addBlog = async (blog) => {
		const config = { headers: { 'Content-Type': 'application/json' } };

		let {
			blog_author,
			blog_headline,
			blog_subtitle,
			category_name,
			header_img_path,
			user_created,
			convertedData,
			rawData,
		} = blog;

		blog_author = blog_author.value;

		try {
			await axios.post(
				`${backend_url}/api/blog/editor/blogs`,
				{
					blog_author,
					blog_headline,
					blog_subtitle,
					category_name,
					header_img_path,
					user_created,
					convertedData,
					rawData,
				},
				config
			);

			dispatch({ type: ADD_BLOG, payload: blog });
		} catch (error) {
			alert(error.response.data);
		}
	};

	//get admin blogs
	const getAdminBlogs = async () => {
		try {
			const res = await axios.get(
				`${backend_url}/api/blog/editor/blogs/adminblogs`
			);
			console.log('hi');
			dispatch({ type: GET_BLOGS, payload: res.data });
		} catch (err) {
			if (
				err.response &&
				err.response.data !== 'Authentication Token Missing'
			)
				alert(err.response.data);
		}
	};

	//get blogs
	const getBlog = async () => {
		try {
			const res = await axios.get(`${backend_url}/api/blog/editor/blogs`);
			dispatch({ type: GET_BLOGS, payload: res.data });
		} catch (err) {
			if (
				err.response &&
				err.response.data !== 'Authentication Token Missing'
			)
				alert(err.response.data);
		}
	};

	const clearBlog = () => {
		dispatch({ type: CLEAR_BLOGS });
	};

	//delete BLOG
	const deleteBlog = async (id) => {
		try {
			await axios.delete(`${backend_url}/api/blog/editor/blogs/${id}`);
			dispatch({ type: DELETE_BLOG, payload: id });
		} catch (err) {
			alert(err.response.data);
		}
	};

	//set current BLOG
	const setCurrent = (blog) => {
		blog.blog_raw = blog.blog_raw.substring(1, blog.blog_raw.length - 1);

		blog.blog_raw = convertFromRaw(JSON.parse(blog.blog_raw));
		blog.editorState = EditorState.createWithContent(blog.blog_raw);
		blog.blog_author = { label: blog.user_name, value: blog.user_name };

		dispatch({ type: SET_CURRENT, payload: blog });
	};

	//clear current BLOG
	const clearCurrent = () => {
		dispatch({ type: CLEAR_CURRENT });
	};

	//upadte the BLOG
	const updateBlog = async (blog) => {
		const config = { headers: { 'Content-Type': 'application/json' } };

		const {
			pk_df_blog_page_id,
			is_published,
			blog_author,
			blog_headline,
			blog_subtitle,
			category_name,
			header_img_path,
			user_created,
			is_editor_pick,
			convertedData,
			rawData,
		} = blog;
		try {
			const res = await axios.put(
				`${backend_url}/api/blog/editor/blogs/${pk_df_blog_page_id}`,
				{
					pk_df_blog_page_id,
					blog_author,
					blog_headline,
					is_published,
					blog_subtitle,
					category_name,
					header_img_path,
					user_created,
					is_editor_pick,
					convertedData,
					rawData,
				},
				config
			);

			dispatch({ type: UPDATE_BLOG, payload: res.data });
		} catch (error) {
			alert(error.response.data);
		}
	};

	return (
		<BlogContext.Provider
			value={{
				blogs: state.blogs,
				current: state.current,
				getAdminBlogs,
				addBlog,
				getBlog,
				clearBlog,
				deleteBlog,
				setCurrent,
				clearCurrent,
				updateBlog,
			}}>
			{props.children}
		</BlogContext.Provider>
	);
};

export default BlogState;
