import React, { Fragment } from 'react';
import { HashRouter as Router, Route, Switch,Redirect } from 'react-router-dom';

import '../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

import Navbar from './Components/layout/Navbar';
import BlogState from './context/blog/BlogState';
//import UserBlogs from './Components/blogs/user/UserBlogs';
import CreateNewBlogForm from './Components/blogs/user/CreateNewBlogForm';
import AuthState from './context/auth/AuthState';
import Login from './Components/auth/Login';
import RegisterPage from './Components/auth/RegisterPage';
import PrivateRoute from './Components/routing/PrivateRoute';
import setAuthToken from './utils/setAuthToken';
import BlogsAdmin from './Components/blogs/admin/BlogsAdmin';
import blogFormAdmin from './Components/blogs/admin/blogFormAdmin';
//import AdminRoute from './Components/routing/AdminRoute';
import AddCategory from './Components/blogs/admin/AddCategory';
import SacForm from './Components/sac/Sacform';
import SacState from './context/sac/SacState';
import Sacs from './Components/sac/Sacs';

if (localStorage.token) {
	setAuthToken(localStorage.token);
}

function App() {
	return (
		<AuthState>
			<BlogState>
				<SacState>
					<Router>
						<Fragment>
							<Navbar />
							<div className='container'>
								<Switch>
									{/* <PrivateRoute
										exact
										path='/'
										component={UserBlogs}
									/> */}

									<PrivateRoute
										exact
										path='/addcategory'
										component={AddCategory}
									/>

									<Route exact path="/">
										<Redirect to="/login" />
									</Route>

									<PrivateRoute
										exact
										path='/register'
										component={RegisterPage}
									/>

									<PrivateRoute
										exact
										path='/blogsadmin'
										component={BlogsAdmin}
									/>

									<PrivateRoute
										exact
										path='/createnewblog'
										component={CreateNewBlogForm}
									/>

									<PrivateRoute
										exact
										path='/formadmin'
										component={blogFormAdmin}
									/>

									<PrivateRoute
										exact
										path='/sac'
										component={SacForm}
									/>

									<PrivateRoute
										exact
										path='/sacs'
										component={Sacs}
									/>

									<Route
										exact
										path='/login'
										component={Login}
									/>
								</Switch>
							</div>
						</Fragment>
					</Router>
				</SacState>
			</BlogState>
		</AuthState>
	);
}

export default App;
