import React, { useState, useContext, useEffect } from 'react';
import BlogContext from '../../../context/blog/blogContext';
import { useHistory } from 'react-router-dom';
import draftToHtml from 'draftjs-to-html';
import { EditorState, convertToRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import AuthContext from '../../../context/auth/authContext';
import axios from 'axios';
import Select from 'react-select';
import backend_url from '../../../url';

const CreateNewBlogForm = () => {
	const blogContext = useContext(BlogContext);
	const authContext = useContext(AuthContext);

	let history = useHistory();

	const [blog, setBlog] = useState({
		pk_df_blog_page_id: '',
		blog_author: '',
		blog_headline: '',
		blog_subtitle: '',
		category_name: 'My Journey',
		header_img_path: '',
		user_created: '',
		actual_link: '',
		is_editor_pick: false,
		is_published: false,
		editorState: EditorState.createEmpty(),
		convertedData: '',
		blog_raw: '',
	});

	//let arr = [];
	const [categories, setCategories] = useState([]);
	const [usernames, setUsernames] = useState([]);

	let {
		blog_headline,
		blog_subtitle,
		category_name,
		header_img_path,
		actual_link,
		editorState,
		blog_author,
	} = blog;

	const { addBlog, updateBlog, current, clearCurrent } = blogContext;

	const clearAll = () => {
		clearCurrent();
	};

	useEffect(() => {
		if (current !== null) {
			setBlog(current);
		} else {
			setBlog({
				pk_df_blog_page_id: '',
				blog_author: '',
				blog_headline: '',
				blog_subtitle: '',
				category_name: 'My Journey',
				header_img_path: '',
				user_created: '',
				actual_link: '',
				is_editor_pick: false,
				is_published: false,
				editorState: EditorState.createEmpty(),
				convertedData: '',
			});
		}
	}, [blogContext, current]);

	const getCategories = async () => {
		await axios
			.get(`${backend_url}/api/blog/editor/blogs/categories`)
			.then((result) => {
				let arr = [];
				result.data.forEach((element) => {
					arr.push(element.category_name);
				});
				setCategories(arr);
			});
	};

	const getUsername = async () => {
		await axios
			.get(`${backend_url}/api/blog/editor/username`)
			.then((result) => {
				let arr = [];
				result.data.forEach((u) => {
					arr.push({ label: u.user_name, value: u.user_name });
				});

				setUsernames(arr);
			});
	};
	useEffect(() => {
		getUsername();
		authContext.loadUser();
		getCategories();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	const onEditorStateChange = (editorState) => {
		setBlog({ ...blog, editorState });
	};

	const onChangeHandler = (e) => {
		const { name, value } = e.target;

		setBlog({ ...blog, [name]: value });
	};

	const handleCategoryName = (option) => {
		setBlog({ ...blog, category_name: option });
	};

	const geturl = (e) => {
		const { value } = e.target;

		setBlog({ ...blog, actual_link: value });

		const arr = value.split('/');
		if (arr.length === 7) {
			setBlog({
				...blog,
				actual_link: `https://drive.google.com/uc?export=view&id=${arr[5]}`,
			});
		} else if (arr.length === 3) {
			setBlog({
				...blog,
				actual_link: `https://drive.google.com/uc?export=view&id=${value.slice(
					33
				)}`,
			});
		} else {
			setBlog({ ...blog, actual_link: 'Please enter a valid link' });
		}
	};

	const handleSelectChange = (blog_author) => {
		setBlog({ ...blog, blog_author });
	};

	const authorSelect = (
		<div className='col'>
			<Select
				placeholder='Blog Author'
				value={blog_author}
				onChange={handleSelectChange}
				options={usernames}
			/>
			<br />
		</div>
	);

	const onSubmit = (e) => {
		e.preventDefault();
		const str = /[-a-zA-Z0-9@:%._~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_.~#?&//=]*)?/gi;
		var regex = new RegExp(str);
		blog.is_published = 0;
		if (current === null) {
			if (header_img_path.match(regex)) {
				blog.convertedData = draftToHtml(
					convertToRaw(editorState.getCurrentContent())
				);

				blog.rawData = JSON.stringify(
					convertToRaw(editorState.getCurrentContent())
				);

				if (
					blog.convertedData.trimLeft().trimRight() === '<p></p>' ||
					blog.blog_author === ''
				) {
					alert('pls enter all the required data');
				} else {
					addBlog(blog);
					clearAll();
					history.push('/blogsadmin');
				}
			} else {
				alert('pls enter a valid header img url');
			}
		} else {
			if (header_img_path.match(regex)) {
				blog.convertedData = draftToHtml(
					convertToRaw(editorState.getCurrentContent())
				);
				blog.rawData = JSON.stringify(
					convertToRaw(editorState.getCurrentContent())
				);
				if (
					blog.convertedData.trimLeft().trimRight() === '<p></p>' &&
					blog.blog_author
				)
					alert('pls enter all the required data');
				else {
					updateBlog(blog);
					clearAll();
					history.push('/blogsadmin');
				}
			} else {
				alert('pls enter a valid header img url');
			}
		}
	};

	return (
		<div>
			<form onSubmit={onSubmit} className='container'>
				<br></br>

				<h4>{current ? 'Edit your Post' : 'Publish a New Post'}</h4>

				<br></br>

				<div className='input-group input-group-lg'>
					<div className='input-group-prepend'>
						<span
							className='input-group-text'
							id='inputGroup-sizing-lg'>
							Title
						</span>
					</div>
					<input
						type='text'
						className='form-control'
						aria-label='Sizing example input'
						aria-describedby='inputGroup-sizing-lg'
						name='blog_headline'
						value={blog_headline}
						required
						onChange={onChangeHandler}></input>
				</div>

				<br></br>
				<div className='input-group mb-3'>
					<div className='input-group-prepend'>
						<span
							className='input-group-text'
							id='inputGroup-sizing-default'>
							Subtitle
						</span>
					</div>
					<input
						type='text'
						className='form-control'
						aria-label='Sizing example input'
						aria-describedby='inputGroup-sizing-default'
						name='blog_subtitle'
						value={blog_subtitle}
						required
						onChange={onChangeHandler}></input>
				</div>

				<div className='row'>
					<div className='col-sm'>
						<div className='input-group mb-3'>
							<div className='input-group-prepend'>
								<span
									className='input-group-text'
									id='inputGroup-sizing-default'>
									Category
								</span>
							</div>

							<div className='dropdown'>
								<button
									className='btn btn-info dropdown-toggle'
									type='button'
									id='dropdownMenuButton'
									data-toggle='dropdown'
									aria-haspopup='true'
									aria-expanded='false'>
									{category_name}
								</button>
								<div
									className='dropdown-menu'
									aria-labelledby='dropdownMenuButton'>
									{categories.map((category) => (
										<button
											key={category}
											onClick={(e) => {
												e.preventDefault();
												handleCategoryName(category);
											}}
											className='dropdown-item'>
											{category}
										</button>
									))}
								</div>
							</div>
						</div>
					</div>
				</div>

				<div className='row col-sm-6'>
					<div className='input-group mb-3'>
						<div className='input-group-prepend'>
							<span
								className='input-group-text'
								id='inputGroup-sizing-default'>
								Enter Google Drive URL
							</span>
						</div>
						<input
							type='text'
							className='form-control'
							aria-label='Sizing example input'
							aria-describedby='inputGroup-sizing-default'
							name='header_img_path'
							onChange={geturl}></input>
					</div>
				</div>

				<div className='row'>
					<div className='col'>
						<div className='input-group mb-3'>
							<div className='input-group-prepend'>
								<span
									className='input-group-text'
									id='inputGroup-sizing-default'>
									Modified Image URL
								</span>
							</div>
							<p style={{ paddingLeft: '1rem' }}>{actual_link}</p>
						</div>
					</div>
				</div>
				<div className='row'>
					<div className='col-sm-6'>
						<div className='input-group mb-3'>
							<div className='input-group-prepend'>
								<span
									className='input-group-text'
									id='inputGroup-sizing-default'>
									Header Image URL
								</span>
							</div>
							<input
								type='text'
								className='form-control'
								aria-label='Sizing example input'
								aria-describedby='inputGroup-sizing-default'
								name='header_img_path'
								value={header_img_path}
								required
								onChange={onChangeHandler}></input>
						</div>
					</div>
				</div>
				<div className='row'>{!current && authorSelect}</div>

				<div className='editor'>
					<Editor
						editorState={editorState}
						wrapperClassName='rich-editor demo-wrapper'
						editorClassName='demo-editor'
						onEditorStateChange={onEditorStateChange}
						required
						placeholder='The message goes here...'
					/>
				</div>
				<br></br>
				<input
					type='submit'
					className='btn btn-primary'
					value={current ? 'Update Blog' : 'Add Blog'}
				/>
				<br />
				<br />

				<br></br>
				<br></br>
			</form>
		</div>
	);
};

export default CreateNewBlogForm;
