import React, { useReducer } from 'react';
import SacContext from './sacContext';
import { EditorState, convertFromRaw } from 'draft-js';
import sacReducer from './sacReducer';
import axios from 'axios';
import backend_url from '../../url';

import {
	DELETE_SAC,
	UPDATE_SAC,
	ADD_SAC,
	GET_SACS,
	SET_CURRENT,
	CLEAR_CURRENT,
	CLEAR_SACS,
} from '../types';

const SacState = (props) => {
	const initialState = {
		sacs: [],
		current: null,
	};
	const [state, dispatch] = useReducer(sacReducer, initialState);

	//add a sac
	const addSac = async (sac) => {
		const config = { headers: { 'Content-Type': 'application/json' } };
		try {
			await axios.post(`${backend_url}/api/child`, sac, config);
			dispatch({ type: ADD_SAC, payload: sac });
		} catch (error) {
			alert(error.response.data);
		}
	};

	//get sacs
	const getSacs = async () => {
		try {
			const res = await axios.get(`${backend_url}/api/child`);
			dispatch({ type: GET_SACS, payload: res.data });
		} catch (error) {
			if (
				error.response &&
				error.response.data !== 'Authentication Token Missing'
			)
				alert(JSON.stringify(error.response.data.err));
		}
	};

	//delete sac
	const deleteSac = async (id) => {
		try {
			await axios.delete(`${backend_url}/api/child/${id}`);
			dispatch({ type: DELETE_SAC, payload: id });
		} catch (err) {
			alert(err.response.data);
		}
	};
	//set current SAC
	const setCurrent = (sac) => {
		sac.child_story = sac.child_story.substring(
			1,
			sac.child_story.length - 1
		);

		sac.child_story = convertFromRaw(JSON.parse(sac.child_story));
		sac.editorState = EditorState.createWithContent(sac.child_story);

		dispatch({ type: SET_CURRENT, payload: sac });
	};

	//clear current BLOG
	const clearCurrent = () => {
		dispatch({ type: CLEAR_CURRENT });
	};

	const clearSac = () => {
		dispatch({ type: CLEAR_SACS });
	};

	//upadte sac
	const updateSac = async (sac) => {
		const config = { headers: { 'Content-Type': 'application/json' } };

		try {
			const res = await axios.put(
				`${backend_url}/api/child/${sac.pk_child_id}`,
				sac,
				config
			);

			dispatch({ type: UPDATE_SAC, payload: res.data });
		} catch (error) {
			alert(error.response.data);
		}
	};

	return (
		<SacContext.Provider
			value={{
				sacs: state.sacs,
				current: state.current,
				addSac,
				getSacs,
				deleteSac,
				setCurrent,
				clearSac,
				clearCurrent,
				updateSac,
			}}>
			{props.children}
		</SacContext.Provider>
	);
};

export default SacState;
