import {
	USER_LOADED,
	AUTH_ERROR,
	LOGIN_FAIL,
	LOGIN_SUCCESS,
	LOGOUT,
	CLEAR_ERRORS,
} from '../types';

export default (state, action) => {
	switch (action.type) {
		case CLEAR_ERRORS:
			return { ...state, error: null };
		case LOGIN_SUCCESS:
			localStorage.setItem('token', action.payload);
			return {
				...state,
				token: action.payload,
				isAuthenticated: true,
				loading: false,
				isAdmin: false,
			};
		case USER_LOADED:
			if (action.payload.user_is_admin) state.isAdmin = true;
			return {
				...state,
				isAuthenticated: true,
				loading: false,
				user: action.payload,
			};
		case AUTH_ERROR:
		case LOGIN_FAIL:
		case LOGOUT:
			localStorage.removeItem('token');
			return {
				...state,
				token: null,
				isAuthenticated: false,
				loading: false,
				user: null,
				error: action.payload,
				isAdmin: false,
			};

		default:
			return state;
	}
};
