import React, { Fragment, useContext, useEffect } from 'react';
import SacContext from '../../context/sac/sacContext';
import SacItem from './SacItem';
import AuthContext from '../../context/auth/authContext';

const Sacs = () => {
	const authContext = useContext(AuthContext);
	const sacContext = useContext(SacContext);

	const { sacs, getSacs, loading } = sacContext;

	useEffect(() => {
		authContext.loadUser();
		getSacs();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	useEffect(() => {
		getSacs();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sacs]);

	return (
		<Fragment>
			{sacs.length !== 0 && !loading ? (
				<div>
					<br></br>
					{sacs.map((sac) => (
						<SacItem key={sac.pk_child_id} sac={sac} />
					))}
				</div>
			) : (
				<div
					style={{ margin: '3rem' }}
					class='alert alert-primary'
					role='alert'>
					You have not added any children yet
				</div>
			)}
		</Fragment>
	);
};

export default Sacs;
