import React, { Fragment, useContext } from 'react';
import PropTypes from 'prop-types';
import SacContext from '../../context/sac/sacContext';
import { useHistory } from 'react-router-dom';

const SacItem = ({ sac }) => {
	const sacContext = useContext(SacContext);

	let history = useHistory();
	const { pk_child_id, first_name, last_name, child_short_story } = sac;

	const onDelete = () => {
		console.log(pk_child_id);
		sacContext.deleteSac(pk_child_id);
		sacContext.clearCurrent();
	};

	const onEdit = () => {
		sacContext.setCurrent(sac);
		history.push('/sac');
	};

	return (
		<Fragment>
			<div
				style={{ padding: '1rem', fontFamily: 'Open Sans, sans-serif' }}
				className='card bg-light'>
				<h3 style={{ fontWeight: 700 }}>
					{first_name} {last_name}
				</h3>
				<h6>{child_short_story}</h6>

				<br></br>

				<p>
					<button className='btn btn-dark btn-sm' onClick={onEdit}>
						Edit
					</button>
					<button
						className='btn btn-danger btn-sm float-right'
						onClick={() => {
							if (window.confirm('Delete the blog')) onDelete();
						}}>
						Delete
					</button>
				</p>
			</div>
			<br></br>
		</Fragment>
	);
};

SacItem.propType = {
	sac: PropTypes.object.isRequired,
};

export default SacItem;
