import {
	GET_SACS,
	ADD_SAC,
	DELETE_SAC,
	CLEAR_CURRENT,
	SET_CURRENT,
	CLEAR_SACS,
	UPDATE_SAC,
} from '../types';

export default (state, action) => {
	switch (action.type) {
		case ADD_SAC:
			return {
				...state,
				sacs: [action.payload, ...state.sacs],
				loading: false,
			};

		case GET_SACS:
			state.sacs.splice(0, state.sacs.length);
			action.payload.forEach((sac) => {
				state.sacs.push(sac);
			});
			return {
				...state,
				loading: false,
			};

		case DELETE_SAC:
			return {
				...state,
				sacs: state.sacs.filter(
					(sac) => sac.pk_child_id !== action.payload
				),
				loading: false,
			};

		case UPDATE_SAC:
			console.log(action.payload);
			return {
				...state,
				sacs: state.sacs.map((sac) =>
					sac.pk_child_id === action.payload.pk_child_id
						? action.payload
						: sac
				),
				loading: false,
			};

		case SET_CURRENT:
			return {
				...state,
				current: action.payload,
			};
		case CLEAR_CURRENT:
			return {
				...state,
				current: null,
			};

		case CLEAR_SACS:
			return {
				...state,
				sacs: [],
				current: null,
			};

		default:
			return state;
	}
};
