import React, { Fragment, useContext } from 'react';
import { Link } from 'react-router-dom';
import AuthContext from '../../context/auth/authContext';
import BlogContext from '../../context/blog/blogContext';
import SacContext from '../../context/sac/sacContext';

const Navbar = () => {
	const authContext = useContext(AuthContext);
	const blogContext = useContext(BlogContext);
	const sacContext = useContext(SacContext);

	const { isAuthenticated, logout } = authContext;

	const onLogout = () => {
		logout();
		blogContext.clearBlog();
		sacContext.clearSac();
	};

	const authLinks = (
		<Fragment>
			<Link
				className='nav-item nav-link active'
				onClick={() => {
					blogContext.clearCurrent();
				}}
				to='/createnewblog'>
				Create a New Blog
			</Link>

			<a className='nav-item nav-link active' onClick={onLogout} href='#'>
				Logout
			</a>
		</Fragment>
	);

	const adminLinks = (
		<Fragment>
			{/* <Link
				className='nav-item nav-link'
				onClick={() => {
					blogContext.clearCurrent();
				}}
				to='/'>
				My Blogs
			</Link> */}

			<Link
				className='nav-item nav-link'
				onClick={() => {
					blogContext.clearCurrent();
				}}
				to='/addcategory'>
				Add Categories
			</Link>

			<Link
				className='nav-item nav-link'
				onClick={() => {
					blogContext.clearCurrent();
				}}
				to='/Register'>
				Add user
			</Link>

			<Link
				className='nav-item nav-link'
				onClick={() => {
					blogContext.clearCurrent();
				}}
				to='/'>
				Admin Dashboard
			</Link>
			<Link
				className='nav-item nav-link'
				onClick={() => {
					sacContext.clearCurrent();
				}}
				to='/sac'>
				Add Child
			</Link>
			<Link
				className='nav-item nav-link'
				onClick={() => {
					sacContext.clearCurrent();
				}}
				to='/sacs'>
				View Children
			</Link>
		</Fragment>
	);

	return (
		<nav className='navbar navbar-expand-lg navbar-dark bg-dark'>
			<a className='navbar-brand' href='#'>
				Desire Foundation - Blog Editor
			</a>

			<button
				className='navbar-toggler'
				type='button'
				data-toggle='collapse'
				data-target='#navbarNavAltMarkup'
				aria-controls='navbarNavAltMarkup'
				aria-expanded='false'
				aria-label='Toggle navigation'>
				<span className='navbar-toggler-icon'></span>
			</button>

			<div className='collapse navbar-collapse' id='navbarNavAltMarkup'>
				<div className='navbar-nav'>
					{isAuthenticated && adminLinks}
				</div>

				<div className='navbar-nav ml-auto'>
					{isAuthenticated && authLinks}
				</div>
			</div>
		</nav>
	);
};

export default Navbar;
