import {
	GET_BLOGS,
	CLEAR_BLOGS,
	ADD_BLOG,
	DELETE_BLOG,
	SET_CURRENT,
	CLEAR_CURRENT,
	UPDATE_BLOG,
} from '../types';

export default (state, action) => {
	switch (action.type) {
		case ADD_BLOG:
			return {
				...state,
				blogs: [...state.blogs, action.payload],
				loading: false,
			};
		case DELETE_BLOG:
			return {
				...state,
				blogs: state.blogs.filter(
					(blog) => blog.pk_df_blog_page_id !== action.payload
				),
				loading: false,
			};
		case SET_CURRENT:
			return {
				...state,
				current: action.payload,
			};
		case CLEAR_CURRENT:
			return {
				...state,
				current: null,
			};
		case UPDATE_BLOG:
			return {
				...state,
				blogs: state.blogs.map((blog) =>
					blog.pk_df_blog_page_id ===
					action.payload.pk_df_blog_page_id
						? action.payload
						: blog
				),
				loading: false,
			};
		case GET_BLOGS:
			state.blogs.splice(0, state.blogs.length);
			action.payload.forEach((blog) => {
				state.blogs.push(blog);
			});
			return {
				...state,
				loading: false,
			};

		case CLEAR_BLOGS:
			return {
				...state,
				blogs: [],
				current: null,
			};
		default:
			return state;
	}
};
